import React, { useState, useEffect } from "react";
import { Button, Row, Col, ListGroup, Image, Card } from "react-bootstrap";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import CheckoutSteps from "../components/CheckoutSteps";
import Message from "../components/Message";
import FormContainer from "../components/FormContainer";
import { createOrder } from "../actions/orderActions";
import {
  ORDER_CREATE_REQUEST,
  ORDER_CREATE_RESET,
} from "../constants/orderConstants";
import Loader from "../components/Loader";

function PlaceOrderScreen({ history }) {
  const orderCreate = useSelector((state) => state.orderCreate);
  const { order, error, success, loading } = orderCreate;

  const dispatch = useDispatch();

  const cart = useSelector((state) => state.cart);

  cart.itemsPrice = cart.cartItems
    .reduce((acc, item) => acc + item.price * item.qty, 0)
    .toFixed(2);
  cart.shippingPrice = (cart.itemsPrice > 499 ? 0 : 50).toFixed(2);
  cart.taxPrice =
    cart.itemsPrice >= 499 && cart.itemsPrice <= 999
      ? Number(0.1 * cart.itemsPrice).toFixed(2)
      : cart.itemsPrice > 999
      ? Number(0.15 * cart.itemsPrice).toFixed(2)
      : 0;

  cart.totalPrice =
    cart.paymentMethod == "COD"
      ? (
          Number(cart.itemsPrice) +
          Number(cart.shippingPrice) -
          Number(cart.taxPrice) +
          40
        ).toFixed(2)
      : (
          Number(cart.itemsPrice) +
          Number(cart.shippingPrice) -
          Number(cart.taxPrice)
        ).toFixed(2);

  if (!cart.paymentMethod) {
    history.push("/payment");
  }

  useEffect(() => {
    if (success) {
      history.push(`/order/${order._id}`);
      dispatch({ type: ORDER_CREATE_RESET });
    }
  }, [success, history]);

  const placeOrder = () => {
    dispatch(
      createOrder({
        orderItems: cart.cartItems,
        shippingAddress: cart.shippingAddress,
        paymentMethod: cart.paymentMethod,
        itemsPrice: cart.itemsPrice,
        shippingPrice: cart.shippingPrice,
        taxPrice: cart.taxPrice,
        totalPrice: cart.totalPrice,
      })
    );
  };
  return (
    <div>
      <CheckoutSteps step1 step2 step3 step4 />
      <Row>
        <Col md={8}>
          <ListGroup variant="flush">
            <ListGroup.Item>
              <h2>Delivery</h2>
              <p>
                <strong>Shipping: </strong>
                {cart.shippingAddress.address}, {cart.shippingAddress.city},
                {"     "}
                {cart.shippingAddress.postalCode}
                <br></br>
                <br></br>
                {cart.shippingAddress.alternatePhone
                  ? `Alternate Phone: ${cart.shippingAddress.alternatePhone}`
                  : ""}
                <br></br>
                <br></br>
                {cart.shippingAddress.alternatePhone
                  ? `Email: ${cart.shippingAddress.alternateEmail}`
                  : ""}
              </p>
            </ListGroup.Item>
            <ListGroup.Item>
              <h2>Order Items</h2>
              {cart.cartItems.length === 0 ? (
                <Message variant="info">Your cart is empty.</Message>
              ) : (
                <ListGroup variant="flush">
                  {cart.cartItems.map((item, index) => (
                    <ListGroup.Item key={index}>
                      <Row>
                        <Col md={2}>
                          <Image
                            src={item.image}
                            alt={item.name}
                            fluid
                            rounded
                          />
                        </Col>
                        <Col>
                          <Link to={`/product/${item.product}`}>
                            {item.name}
                          </Link>
                        </Col>
                        <Col md={1}>{item.size}UK</Col>
                        <Col md={4}>
                          {item.qty} x {item.price} = 
                          {(item.qty * item.price).toFixed(2)}
                        </Col>
                      </Row>
                    </ListGroup.Item>
                  ))}
                </ListGroup>
              )}
            </ListGroup.Item>
          </ListGroup>
        </Col>
        <Col md={4}>
          <Card>
            <ListGroup variant="flush">
              <ListGroup.Item>
                <h2>Order Summary</h2>
              </ListGroup.Item>
              <ListGroup.Item>
                <Row>
                  <Col>Item:</Col>
                  <Col>{cart.itemsPrice}</Col>
                </Row>
              </ListGroup.Item>
              <ListGroup.Item>
                <Row>
                  <Col>Delivery:</Col>
                  <Col>{cart.shippingPrice}</Col>
                </Row>
              </ListGroup.Item>
              <ListGroup.Item
                style={{
                  display: cart.paymentMethod !== "COD" && "none",
                }}
              >
                <Row>
                  <Col>COD Charges:</Col>
                  <Col>40</Col>
                </Row>
              </ListGroup.Item>
              <ListGroup.Item disabled={cart.taxPrice === 0}>
                <Row
                  style={{
                    color: cart.taxPrice != 0 && "#6666FF",
                    fontFamily: cart.taxPrice != 0 && "Alegreya",
                    fontSize: cart.taxPrice != 0 && "18px",
                  }}
                >
                  <Col>Discount:</Col>
                  <Col>
                    <strong>{cart.taxPrice}</strong>
                  </Col>
                </Row>
              </ListGroup.Item>
              <ListGroup.Item>
                <Row>
                  <Col>Total:</Col>
                  <Col
                    style={{
                      textDecoration: "line-through",
                      right: "1rem",
                      visibility:
                        Number(cart.taxPrice) == 0 ? "hidden" : "visible",
                    }}
                  >
                    {Number(cart.itemsPrice) + Number(cart.shippingPrice)}
                  </Col>
                  <Col style={{ right: "3.5rem" }}>{cart.totalPrice}</Col>
                </Row>
              </ListGroup.Item>
              <ListGroup.Item
                style={{
                  visibility:
                    Number(cart.itemsPrice) +
                      Number(cart.shippingPrice) -
                      Number(cart.totalPrice) ===
                    0
                      ? "hidden"
                      : "visible",
                  display:
                    Number(cart.itemsPrice) +
                      Number(cart.shippingPrice) -
                      Number(cart.totalPrice) <=
                      0 && "none",
                  background: "#28a745",
                  color: "white",
                }}
              >
                Your total savings{" "}
                <strong style={{ fontWeight: "bold", fontSize: "1.10rem" }}>
                  
                  {(
                    Number(cart.itemsPrice) +
                    Number(cart.shippingPrice) -
                    Number(cart.totalPrice)
                  ).toFixed(2)}
                </strong>
              </ListGroup.Item>
              <ListGroup.Item>
                {error && <Message variant="danger">{error}</Message>}
              </ListGroup.Item>
              <ListGroup.Item>{loading && <Loader />}</ListGroup.Item>
              <ListGroup.Item>
                <Button
                  type="button"
                  className="btn-block"
                  disabled={cart.cartItems === 0}
                  onClick={placeOrder}
                >
                  Place Order
                </Button>
              </ListGroup.Item>
            </ListGroup>
          </Card>
        </Col>
      </Row>
    </div>
  );
}

export default PlaceOrderScreen;
