import React, { useEffect, useState } from "react";
import {
  Col,
  Image,
  ListGroup,
  Row,
  Form,
  Card,
  Button,
} from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
import {
  listProductDetails,
  createProductReview,
} from "../actions/productActions";
import Rating from "../components/Rating";
import Loader from "../components/Loader";
import Message from "../components/Message";
import Popup from "reactjs-popup";
import { PRODUCT_CREATE_REVIEW_RESET } from "../constants/productConstants";
import ReactImageMagnify from "react-image-magnify";

function ProductScreen({ match, history }) {
  const [qty, setQty] = useState(1);
  const [size, setSize] = useState(4);
  const [rating, setRating] = useState(0);
  const [comment, setComment] = useState("");

  const dispatch = useDispatch();
  const closeLightbox = () => {
    this.state.open = true;
  };
  const productDetails = useSelector((state) => state.productDetails);
  const { loading, error, product } = productDetails;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const productReviewCreate = useSelector((state) => state.productReviewCreate);
  const {
    loading: loadingProductReview,
    error: errorProductReview,
    success: successProductReview,
  } = productReviewCreate;

  useEffect(() => {
    if (successProductReview) {
      setRating(0);
      setComment("");
      dispatch({ type: PRODUCT_CREATE_REVIEW_RESET });
    }

    dispatch(listProductDetails(match.params.id));
  }, [dispatch, match, successProductReview]);

  const addToCartHandler = () => {
    history.push(`/cart/${match.params.id}?qty=${qty}&size=${size}`);
  };

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(
      createProductReview(match.params.id, {
        rating,
        comment,
      })
    );
  };
  return (
    <div>
      <Link to="/" className="btn btn-light my-3">
        <i className="fas fa-arrow-left mr-1"></i>
        GO BACK
      </Link>
      {loading ? (
        <Loader />
      ) : error ? (
        <Message variant="danger">{error}</Message>
      ) : (
        <div>
          <Row>
            <Col md={6}>
              <ReactImageMagnify
                {...{
                  smallImage: {
                    alt: product.name,
                    isFluidWidth: true,
                    src: product.image,
                  },
                  largeImage: {
                    src: product.image,
                    width: 1200,
                    height: 1200,
                  },
                  enlargedImagePosition: "over",
                }}
              />
              {/* <Image src={product.image} alt={product.name} fluid /> */}
            </Col>
            <Col md={3}>
              <ListGroup variant="flush">
                <ListGroup.Item>
                  <h3>{product.name}</h3>
                </ListGroup.Item>
                <ListGroup.Item>
                  <Rating
                    value={product.rating}
                    text={`${product.numReviews} reviews`}
                    color="#f8e825"
                  />
                </ListGroup.Item>
                <ListGroup.Item> Price:  {product.price} </ListGroup.Item>
                <ListGroup.Item>
                  Description: {product.description}
                </ListGroup.Item>
              </ListGroup>
            </Col>
            <Col md={3}>
              <Popup
                trigger={<Button variant="outline-dark">Size Chart</Button>}
                position="bottom"
              >
                <div style={{ position: "absolute" }}>
                  <Image src="https://res.cloudinary.com/sakshamtolani/image/upload/v1643279058/sizeChart_wzasap.jpg" />
                </div>
              </Popup>
              <Card>
                <ListGroup variant="flush">
                  <ListGroup.Item>
                    <Row>
                      <Col>Price:</Col>
                      <Col className="my-1">
                        <strong>
                          <i className="fas fa-rupee-sign"></i>
                          {product.price}
                        </strong>
                        <Col>
                          <span
                            style={{
                              textDecorationLine: "line-through",
                              textDecorationStyle: "solid",
                              color: "#b7b7b7",
                            }}
                            className="price-wrapper"
                          >
                            {product.lastPrice}
                          </span>
                        </Col>
                      </Col>
                    </Row>
                  </ListGroup.Item>
                  <ListGroup.Item>
                    <Row>
                      <Col>Status:</Col>
                      <Col>
                        {product.countInStock > 0 ? "In Stock" : "Out of Stock"}
                      </Col>
                    </Row>
                  </ListGroup.Item>
                  {product.countInStock > 0 && (
                    <ListGroup.Item>
                      <Row>
                        <Col>Qty</Col>
                        <Col cs="auto" className="my-1">
                          <Form.Control
                            as="select"
                            value={qty}
                            onChange={(e) => setQty(e.target.value)}
                          >
                            {[...Array(product.countInStock).keys()].map(
                              (x) => (
                                <option key={x + 1} value={x + 1}>
                                  {x + 1}
                                </option>
                              )
                            )}
                          </Form.Control>
                        </Col>
                      </Row>
                    </ListGroup.Item>
                  )}
                  {product.sizes && product.countInStock > 0 && (
                    <ListGroup.Item>
                      <Row>
                        <Col>Size</Col>
                        <Col cs="auto" className="my-1">
                          <Form.Control
                            as="select"
                            value={size}
                            style={{ paddingRight: "5px" }}
                            onChange={(e) => setSize(e.target.value)}
                          >
                            {product.sizes.split("").map((s) => (
                              <option key={s} value={s}>
                                {s}UK
                              </option>
                            ))}
                          </Form.Control>
                        </Col>
                      </Row>
                    </ListGroup.Item>
                  )}
                  <ListGroup.Item>
                    <Button
                      disabled={product.countInStock == 0}
                      className="btn-block"
                      type="button"
                      onClick={addToCartHandler}
                    >
                      {product.countInStock !== 0
                        ? "Add to Cart"
                        : "Out of Stock"}
                    </Button>
                  </ListGroup.Item>
                </ListGroup>
              </Card>
            </Col>
          </Row>
          <Row className="mt-4">
            <Col md={6}>
              <h4>Reviews</h4>
              {product.reviews.length == 0 && (
                <Message variant="info">No Reviews</Message>
              )}

              <ListGroup variant="flush">
                {product.reviews.map((review) => (
                  <ListGroup.Item key={review._id}>
                    <strong className="font-weight-bold">{review.name}</strong>{" "}
                    <p>{review.createdAt.split("T")[0]}</p>
                    <p>{review.comment}</p>
                    <Rating value={review.rating} color="#f8e825" />
                  </ListGroup.Item>
                ))}

                <ListGroup.Item>
                  <h4>Write a Review</h4>
                  {loadingProductReview && <Loader />}
                  {successProductReview && (
                    <Message variant="success">Review Submitted</Message>
                  )}
                  {errorProductReview && (
                    <Message variant="danger">{errorProductReview}</Message>
                  )}

                  {userInfo ? (
                    <Form onSubmit={submitHandler}>
                      <Form.Group controlId="rating">
                        <Form.Label>Rating</Form.Label>
                        <Form.Control
                          as="select"
                          value={rating}
                          onChange={(e) => setRating(e.target.value)}
                        >
                          <option value="">Select...</option>
                          <option value="1">1 - Poor</option>
                          <option value="2">2 - Fair</option>
                          <option value="3">3 - Good</option>
                          <option value="4">4 - Very Good</option>
                          <option value="5">5 - Excellent</option>
                        </Form.Control>
                      </Form.Group>
                      <Form.Group controlId="comment" className="mt-2">
                        <Form.Label>Comment</Form.Label>
                        <Form.Control
                          as="textarea"
                          row="5"
                          value={comment}
                          onChange={(e) => setComment(e.target.value)}
                        ></Form.Control>
                      </Form.Group>
                      <Button
                        disabled={loadingProductReview}
                        type="submit"
                        variant="primary"
                        className="mt-4"
                      >
                        Submit
                      </Button>
                    </Form>
                  ) : (
                    <Message variant="info">
                      Please <Link to="/login">Login</Link> to write a review.
                    </Message>
                  )}
                </ListGroup.Item>
              </ListGroup>
            </Col>
          </Row>
        </div>
      )}
    </div>
  );
}

export default ProductScreen;
