import React, { useState, useEffect } from "react";
import { Form, Button } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import CheckoutSteps from "../components/CheckoutSteps";
import FormContainer from "../components/FormContainer";
import { saveShippingAddress } from "../actions/cartActions";

function ShippingScreen({ history }) {
  const cart = useSelector((state) => state.cart);
  const { shippingAddress } = cart;
  const [address, setAddress] = useState(shippingAddress.address);
  const [city, setCity] = useState("Varanasi");
  const [postalCode, setPostalCode] = useState(shippingAddress.postalCode);
  const [alternatePhone, setAlternatePhone] = useState(
    shippingAddress.alternatePhone
  );
  const dispatch = useDispatch();
  const [alternateEmail, setAlternateEmail] = useState(
    shippingAddress.alternateEmail
  );
  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(
      saveShippingAddress({
        address,
        city,
        postalCode,
        alternatePhone,
        alternateEmail,
      })
    );
    history.push("/payment");
  };
  return (
    <FormContainer>
      <CheckoutSteps step1 step2 />
      <h1>Shipping</h1>
      <Form onSubmit={submitHandler}>
        <Form.Group className="mb-3" controlId="address">
          <Form.Label>Address</Form.Label>
          <Form.Control
            required
            type="text"
            placeholder="Enter Address"
            value={address ? address : ""}
            onChange={(e) => setAddress(e.target.value)}
          ></Form.Control>
        </Form.Group>
        <Form.Group className="mb-3" controlId="city">
          <Form.Label>City</Form.Label>
          <Form.Control
            required
            type="text"
            placeholder="Enter City"
            value={city ? city : ""}
            onChange={(e) => setCity(e.target.value)}
          ></Form.Control>
        </Form.Group>
        <Form.Group className="mb-3" controlId="postalCode">
          <Form.Label>Pin Code</Form.Label>
          <Form.Control
            required
            type="text"
            placeholder="Enter Pin Code"
            value={postalCode ? postalCode : ""}
            onChange={(e) => setPostalCode(e.target.value)}
          ></Form.Control>
        </Form.Group>
        <Form.Group className="mb-3" controlId="alternatePhone">
          <Form.Label>Alternate Phone Number</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter Phone Number"
            value={alternatePhone ? alternatePhone : ""}
            onChange={(e) => setAlternatePhone(e.target.value)}
          />
          <Form.Text className="text-muted">
            Please enter any alternate number which can be used while delievery.
          </Form.Text>
        </Form.Group>
        <Form.Group className="mb-3" controlId="alternateEmail">
          <Form.Label>Email (A step to make our bond stronger )</Form.Label>
          <Form.Control
            type="email"
            placeholder="Enter Email"
            value={alternateEmail ? alternateEmail : ""}
            onChange={(e) => setAlternateEmail(e.target.value)}
          />
        </Form.Group>
        <Button type="submit" variant="primary">
          CONTINUE TO PAY
        </Button>
      </Form>
    </FormContainer>
  );
}

export default ShippingScreen;
